import React, { Component } from "react";
import { getPrimaryTextColor, getPrimarySideColor, getBackgroundColor, isMobile } from "../../app/adminHelper";
import { Card } from "../../components/Card/Card"
import { Col, Row, ProgressBar } from "react-bootstrap";
import ReactSpeedometer from "react-d3-speedometer"

class Indicators extends Component {

    renderSpeedo = (title, value, maxValue, segments, bottomString) => {

        let outerWidth = window.innerWidth < 400 ? 250 : 300;
        let innerWidth = outerWidth - 40;

        return (
            <div style={{ width: outerWidth, height: "220px ", borderStyle: 'solid', borderRadius: '5px', borderColor: getPrimarySideColor() }}>
                <div style={{ width: innerWidth }}>
                    <div>
                        <h5><b><font color={getPrimarySideColor()}>{title}</font></b></h5>
                    </div>
                    <ReactSpeedometer
                        maxValue={maxValue}
                        value={value}
                        width={innerWidth}
                        needleColor={getPrimarySideColor()}
                        textColor={getPrimarySideColor()}
                        segments={segments}
                        currentValueText={bottomString}
                        needleHeightRatio={0.6}
                        needleTransitionDuration={2000}
                        needleTransition="easeBackOut"
                    />
                </div>
            </div>);
    }

    renderTotalLiquids = () => {
        let bottomString = "24.5 KL";
        return this.renderSpeedo("Total Water In Building", 24.5, 30, 8, bottomString);
    }

    renderExtinguishing = () => {
        let bottomString = "76 %";
        return this.renderSpeedo("Extinguishing Reserved", 76, 100, 3, bottomString);
    }

    renderStatus = () => {
        //success, warning , danger
        let bsStyle = "danger";
        let label = <font color={getPrimarySideColor()}><b>ERROR</b></font>
        return (
            <div>
                <br />
                <div style={{ width:'40%', borderStyle: 'solid', borderRadius: '5px', borderColor: getPrimarySideColor() }}>
                    <h5><b><font color={getPrimarySideColor()}>System Condition</font></b></h5>
                    <ProgressBar active bsStyle={bsStyle} now={100} key={3} label={label} style={{ width: '90%' }} />
                    <h5><b><font color={getPrimarySideColor()}>Water level are low</font></b></h5>
                </div>
            </div>

        )
    }

    render() {
        let height = this.props.fromDashboard === true ? '' : '120%';
        let title = "SYSTEM HELTH";
        return (
            <div className="content" style={{ backgroundColor: getBackgroundColor(), height: height }}>
                {isMobile() &&
                    <Col xs={12} >
                        <Card
                            hCenter={true}
                            id="chartHours"
                            title={title}
                            color={getPrimaryTextColor()}
                            content={
                                <div>
                                    <center>
                                        <Row>
                                            <Col xs={12} >
                                                {this.renderTotalLiquids()}
                                            </Col>
                                        </Row>
                                        <br />
                                        <Row>
                                            <Col xs={12}>
                                                {this.renderExtinguishing()}
                                            </Col >
                                        </Row>
                                        <Row>
                                            <Col xs={12}>
                                                <div>
                                                    {this.renderStatus()}
                                                </div>
                                            </Col>
                                        </Row>
                                    </center>
                                </div>
                            }
                        />
                    </Col>
                }
                {isMobile() === false &&
                    <Col xs={12} >
                        <Card
                            hCenter={true}
                            id="chartHours"
                            title={title}
                            color={getPrimaryTextColor()}
                            content={
                                <div>
                                    <center>
                                        <Row>
                                            <Col xs={6} >
                                                {this.renderTotalLiquids()}
                                            </Col>
                                            <Col xs={6}>
                                                {this.renderExtinguishing()}
                                            </Col >
                                        </Row>
                                        <Row>
                                            <Col xs={12}>
                                                <div>
                                                    {this.renderStatus()}
                                                </div>
                                            </Col>
                                        </Row>
                                    </center>
                                </div>
                            }
                        />
                    </Col>
                }
            </div>
        )
    }
}

export default Indicators;
